<template>
	<article id="content" class="content">
		<ul class="breadcrumb">
			<li class="home"><a href="javascript:void(0);">홈</a></li>
			<li><a href="javascript:void(0);">메시지 발송</a></li>
			<li><span>RCS 발송</span></li>
		</ul>
		<div class="bundle_wrap">
			<div class="emul_form_wrap">
				<div class="emul_form_head">
					<h2 class="title">RCS 발송</h2>
					<span class="text_h2_desc">발송할 템플릿을 선택 후 발송할 수 있습니다.</span>
					<a href="javascript:void(0);" class="btn_link" @click.prevent="layerId = 'msgBox'">
						<span>메시지보관함 </span>
						<div class="box_tooltip">
							<p>서비스 준비중</p>
						</div>
					</a>
				</div>
				<div class="emul_form_cont">
					<div class="item_bundle">
						<strong class="tit_bundle">브랜드</strong>
						<div class="cont_bundle">
							<!-- 상세 -->
							<span class="custom_select full_width"
								v-if="detailType == '타이틀 선택형(서술형)' || detailType == '타이틀 선택형(스타일)' || detailType == '타이틀 자유형(서술형)' || detailType == '타이틀 자유형(스타일)' || detailType == '단문형(SMS)' || detailType == '장문형(LMS)' || detailType == '프리 템플릿'">
								<select>
									<option>브랜드 선택</option>
									<option selected>위디어</option>
								</select>
							</span>
							<!-- 등록 -->
							<span class="custom_select full_width" v-else>
								<select>
									<option>브랜드 선택</option>
									<option>위디어</option>
								</select>
							</span>
						</div>
					</div>
					<div class="item_bundle">
						<strong class="tit_bundle">RCS 승인여부</strong>
						<div class="box_add_radio">
							<span class="custom_radio">
								<input type="radio" id="rcsDvs01" v-model="rcsDvs" value="승인형">
								<label for="rcsDvs01">승인형</label>
							</span>
							<span class="custom_radio">
								<input type="radio" id="rcsDvs02" v-model="rcsDvs" value="비승인형">
								<label for="rcsDvs02">비승인형</label>
							</span>
						</div>
					</div>
					<div class="item_bundle">
						<strong class="tit_bundle">RCS 템플릿 선택</strong>
						<div class="cont_bundle">
							<div class="btn_wrap lookup">
								<a href="javascript:void(0);" class="btn sml gray" @click.prevent="layerId = 'tmpLookup'">
									<span>템플릿 조회</span>
								</a>
								<div class="txt">템플릿 명 / 템플릿 ID</div>
							</div>
							<!-- 템플릿 조회 팝업 예시 -->
							<temp-layer-pop v-if="layerId == 'tmpLookup'" @changeLayerId="changeLayerId" />
						</div>
					</div>

					<div class="tab_wrap" v-if="isLoadTemplate">
						<ul class="tab_nav">
							<li @click="myFilter($event)" tab-id="0" class="active"><a href="javascript:void(0);">메시지 작성</a></li>
							<li @click="myFilter($event)" tab-id="1"><a href="javascript:void(0);">발송설정</a></li>
						</ul>
						<div class="tab_container">
							<div class="tab_content active">
								<h2 class="blind">메시지작성 탭</h2>
								<div class="item_bundle_wrap">
									<div class="item_bundle">
										<strong class="tit_bundle pad_t0">템플릿 유형</strong>
										<div class="cont_bundle" v-if="detailType == '타이틀 선택형(서술형)'">타이틀 서술형(서술형)</div>
										<div class="cont_bundle" v-if="detailType == '타이틀 선택형(스타일)'">타이틀 서술형(스타일)</div>
										<div class="cont_bundle" v-if="detailType == '타이틀 자유형(서술형)'">타이틀 자유형(서술형)</div>
										<div class="cont_bundle" v-if="detailType == '타이틀 자유형(스타일)'">타이틀 자유형(스타일)</div>
										<div class="cont_bundle" v-if="detailType == '단문형(SMS)'">단문형(SMS)</div>
										<div class="cont_bundle" v-if="detailType == '장문형(LMS)'">장문형(LMS)</div>
										<div class="cont_bundle" v-if="detailType == '프리 템플릿'">프리 템플릿</div>
										<div class="cont_bundle" v-if="detailType == ''">타이틀 자유형</div>
									</div>
									<div class="item_bundle"
										v-if="(rcsDvs == '승인형' && detailType == '타이틀 선택형(서술형)') || (rcsDvs == '승인형' && detailType == '타이틀 선택형(스타일)')">
										<strong class="tit_bundle">유형</strong>
										<div class="cont_bundle">
											<!-- 상세 -->
											<span class="custom_select full_width"
												v-if="(rcsDvs == '승인형' && detailType == '타이틀 선택형(서술형)') || (rcsDvs == '승인형' && detailType == '타이틀 선택형(스타일)')">
												<select disabled>
													<option>유형 선택</option>
													<option selected>출고</option>
												</select>
											</span>
											<!-- 등록 -->
											<span class="custom_select full_width" v-else>
												<select>
													<option>유형 선택</option>
													<option>출고</option>
												</select>
											</span>
										</div>
									</div>
									<div class="item_bundle">
										<strong class="tit_bundle">버튼 노출</strong>
										<!-- 상세 -->
										<div class="cont_bundle"
											v-if="detailType == '타이틀 선택형(서술형)' || detailType == '타이틀 선택형(스타일)' || detailType == '타이틀 자유형(서술형)' || detailType == '타이틀 자유형(스타일)' || detailType == '단문형(SMS)' || detailType == '장문형(LMS)' || detailType == '프리 템플릿'">
											<span class="custom_radio">
												<input type="radio" v-model="buttonCount" name="button" id="button01" value="0" disabled>
												<label for="button01">미사용</label>
											</span>
											<span class="custom_radio">
												<input type="radio" v-model="buttonCount" name="button" id="button02" value="1" disabled>
												<label for="button02">1개</label>
											</span>
											<span class="custom_radio" v-if="detailType != '프리 템플릿'">
												<input type="radio" v-model="buttonCount" name="button" id="button03" value="2" disabled>
												<label for="button03">2개</label>
											</span>
											<span class="custom_radio" v-if="detailType != '프리 템플릿'">
												<input type="radio" v-model="buttonCount" name="button" id="button04" value="3" disabled>
												<label for="button04">3개</label>
											</span>
										</div>
										<!-- 등록 -->
										<div class="cont_bundle" v-else>
											<span class="custom_radio">
												<input type="radio" v-model="buttonCount" name="button" id="button01" value="0">
												<label for="button01">미사용</label>
											</span>
											<span class="custom_radio">
												<input type="radio" v-model="buttonCount" name="button" id="button02" value="1">
												<label for="button02">1개</label>
											</span>
											<span class="custom_radio">
												<input type="radio" v-model="buttonCount" name="button" id="button03" value="2">
												<label for="button03">2개</label>
											</span>
											<span class="custom_radio">
												<input type="radio" v-model="buttonCount" name="button" id="button04" value="3">
												<label for="button04">3개</label>
											</span>
										</div>
									</div>

									<button-maker :count="buttonCount" />

									<span class="custom_checkbox fs14 mar_t10 mar_b30" v-if="rcsDvs == '승인형'">
										<input type="checkbox" id="agree01">
										<label for="agree01" class="i_block">
											정보성 메시지만 보낼 수 있으며, 광고 등 정책에 위배되는 메시지 발송 시 템플릿 사용이 중지될 수 있음을 동의합니다.
										</label>
									</span>
								</div>
							</div>
							<div class="tab_content">
								<h2 class="blind">발송설정 탭</h2>
								<div class="item_bundle_wrap">
									<receiver-set-box sendNum />
								</div>
							</div>
						</div>
					</div>

					<div class="default_guide_wrap" v-if="!isLoadTemplate">
						<reg-check-guide-rcs />
					</div>

					<!-- 승인 프로세스 -->
					<approval-form-box />

				</div>
			</div>

			<!-- emulator -->
			<emulator-view-box :buttonCount="this.buttonCount" @changeLayerPop="changeLayerPop" />

			<!-- 메시지보관함 팝업 -->
			<msg-store-box-layer-rcs v-if="layerId == 'msgBox'" @changeLayerId="changeLayerId" />

			<!-- 테스트 발송 팝업 -->
			<test-sending-layer v-if="layerId == 'testSending'" @changeLayerId="changeLayerId" />


		</div>
	</article>
</template>

<script>
import EmulatorViewBox from '../components/EmulatorViewBox';
import ReceiverSetBox from '../components/ReceiverSetBox';
import TempLayerPop from '../components/TempLayerPop';
import ApprovalFormBox from '../components/ApprovalFormBox';
import TestSendingLayer from '../components/TestSendingLayer';
import RegCheckGuideRcs from '../components/RegCheckGuideRcs';
import ButtonMaker from '../components/ButtonMaker';
import MsgStoreBoxLayerRcs from '../components/MsgStoreBoxLayerRcs';

export default {
	name: 'smsSendPage',
	data() {
		return {
			layerId: '',
			isLoadTemplate: true,
			// 타이틀 선택형(서술형), 타이틀 선택형(스타일), 타이틀 자유형(서술형), 타이틀 자유형(스타일), 단문형(SMS), 장문형(LMS), 프리 템플릿
			detailType: '',
			rcsDvs: '승인형',
			buttonCount: 0,
		};
	},
	components: {
		EmulatorViewBox,
		ReceiverSetBox,
		TempLayerPop,
		ApprovalFormBox,
		TestSendingLayer,
		RegCheckGuideRcs,
		ButtonMaker,
		MsgStoreBoxLayerRcs,
	},
	methods: {
		myFilter: function (event) {
			var myIndex = event.target.parentElement.getAttribute('tab-id');
			var myLi = document.querySelectorAll('.emul_form_cont .tab_nav > li');
			var myTabContent = document.querySelectorAll('.emul_form_cont .tab_container > div');
			for (var i = 0; i < myLi.length; i++) {
				myLi[i].classList.remove('active');
				myTabContent[i].classList.remove('active');
			}
			myLi[myIndex].classList.add('active');
			myTabContent[myIndex].classList.add('active');
		},
		changeLayerId(layerId) {
			this.layerId = layerId;
		},
		changeLayerPop(layerId) {
			this.layerId = layerId;
		},
	}
};
</script>